import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import { MarketService } from '../core/market.service';
import { PetService } from '../core/pet.service';
import { UtilityService } from '../core/utility.service';
import { cat } from 'src/app/signup/cat-breed';
import { dog } from 'src/app/signup/dog-breed';

@Component({
  selector: 'app-add-edit-pet',
  templateUrl: './add-edit-pet.page.html',
  styleUrls: ['./add-edit-pet.page.scss'],
})
export class AddEditPetPage implements OnInit {
  model: Pet = {
    name: '',
    age: '',
    color: '',
    type: '',
    breed: '',
    vaccineName: '',
    vaccineDate: '',
    noOfDose: '',
    currentDose: '',
    nextVaccineDate: '',
    remedyName: '',
    treatmentStartDate: '',
    noOfTreatmentPerDay: '',
    finalDate: '',
    food: '',
    image: '',
    qrCode: '',
  };
  file;
  pageLabel = 'Add pet';
  previewImage: any = '';
  btnName = 'Save';
  id = null;
  petTypes;
  dogBreed = dog;
  catBreed = cat;
  constructor(
    private petService: PetService,
    private sanitizer: DomSanitizer,
    private utility: UtilityService,
    private activeRoute: ActivatedRoute,
    private marketService: MarketService
  ) {}

  ngOnInit() {
    this.getParams();
    this.getPetTypes();
  }

  handleFile(e) {
    this.file = e.target.files[0];
    this.model.image = this.file;
    this.previewImage = this.sanitizer.bypassSecurityTrustUrl(
      URL.createObjectURL(this.file)
    );
  }
  removeImage() {
    this.previewImage = '';
    this.model.image = '';
  }
  save(form) {
    if (form.status.toLowerCase() == 'invalid') {
      return;
    }
    if (this.model.image == '' && this.previewImage == '') {
      return this.utility.showAlert('Image is required');
    }
    this.utility.present();
    const fd = new FormData();
    fd.append('name', this.model.name);
    fd.append('age', this.model.age);
    fd.append('color', this.model.color);
    fd.append('breed', this.model.breed);
    fd.append('type', this.model.type);
    fd.append('vaccineName', this.model.vaccineName);
    fd.append('vaccineDate', this.model.vaccineDate);
    fd.append('noOfDose', this.model.noOfDose);
    fd.append('nextVaccineDate', this.model.nextVaccineDate);
    fd.append('remedyName', this.model.remedyName);
    fd.append('treatmentStartDate', this.model.treatmentStartDate);
    fd.append('noOfTreatmentPerDay', this.model.noOfTreatmentPerDay);
    fd.append('finalDate', this.model.finalDate);
    fd.append('food', this.model.food);
    fd.append('image', this.model.image);
    if (this.id == null) {
      this.petService.petAdd(fd).subscribe(
        (res) => {
          this.utility.dismiss();
          this.utility.navigateRoot('/tabs/profile-details');
        },
        (err) => {
          this.utility.dismiss();
          this.utility.showAlert('Something went wrong!');
        }
      );
    } else {
      this.petService.petUpdate(this.id, fd).subscribe(
        (res) => {
          this.utility.dismiss();
          this.utility.navigateRoot('/tabs/profile');
        },
        (err) => {
          this.utility.dismiss();
          this.utility.showAlert('Something went wrong!');
        }
      );
    }
  }
  getSinglePet(id) {
    this.utility.present();
    this.petService.getPetSingle(id).subscribe((res) => {
      const {
        name,
        age,
        color,
        food,
        vaccineName,
        vaccineDate,
        remedyName,
        treatmentStartDate,
        noOfTreatmentPerDay,
        noOfDose,
        nextVaccineDate,
        finalDate,
        currentDose,
        image,
        type,
        breed,
        imageUrl,
      } = res;

      this.getPetValue(breed);
      this.model.name = name;
      this.model.age = age;
      this.model.color = color;
      this.model.food = food;
      this.model.vaccineName = vaccineName;
      this.model.vaccineDate = vaccineDate;
      this.model.remedyName = remedyName;
      this.model.treatmentStartDate = treatmentStartDate;
      this.model.noOfTreatmentPerDay = noOfTreatmentPerDay;
      this.model.noOfDose = noOfDose;
      this.model.nextVaccineDate = nextVaccineDate;
      this.model.finalDate = finalDate;
      this.model.currentDose = currentDose;
      this.model.image = image;

      this.model.breed = breed;
      this.previewImage = imageUrl || '';
      setTimeout(() => {
        this.model.type = type;
        this.utility.dismiss();
      }, 1000);
      // this.model = res;
    });
  }

  getParams() {
    this.activeRoute.params.subscribe((res) => {
      this.model = {
        name: '',
        age: '',
        color: '',
        type: '',
        breed: '',
        vaccineName: '',
        vaccineDate: '',
        noOfDose: '',
        currentDose: '',
        nextVaccineDate: '',
        remedyName: '',
        treatmentStartDate: '',
        noOfTreatmentPerDay: '',
        finalDate: '',
        food: '',
        image: '',
        qrCode: '',
      };
    });
    let id = this.activeRoute.snapshot.params.id;
    if (id != undefined && id != null) {
      this.getSinglePet(id);
      this.btnName = 'Update';
      this.pageLabel = 'Update Pet';

      this.id = id;
    }
  }
  goBack() {
    this.utility.goBack();
  }
  getPetTypes() {
    this.marketService.getPetType().subscribe((res) => {
      this.petTypes = res;
    });
  }
  deletePet() {
    this.utility.confirmationAlert((cb) => {
      if (cb) {
        this.petService.petDelete(this.id).subscribe((res) => {
          this.utility.navigateRoot('/tabs/profile');
        });
      }
    });
  }

  getPetValue(value) {
    if (value != '') {
      return value == 'dog';
    } else {
      return false;
    }
  }
  handlePetTypeChange() {
    this.model.breed = '';
  }
}

interface Pet {
  name: string;
  age: string;
  color: string;
  breed: string;
  type: string;
  vaccineName: string;
  vaccineDate: string;
  noOfDose: string;
  currentDose: string;
  nextVaccineDate: string;
  remedyName: string;
  treatmentStartDate: string;
  noOfTreatmentPerDay: string;
  finalDate: string;
  food: string;
  image: string;
  userId?: string;
  qrCode?: string;
}
