import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AddEditPetPage } from './add-edit-pet.page';

const routes: Routes = [
  {
    path: '',
    component: AddEditPetPage,
  },
  {
    path: ':id',
    component: AddEditPetPage,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AddEditPetPageRoutingModule {}
